html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  background-color: #ebe7de;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

.link-active {
  color: #c39c63 !important;
}

h2, .heading-white {
  color: #5e311e;
}
h2 span, .heading-white span {
  display: block;
  font-size: 1.8rem;
  color: #c39c63;
}
h2::after, .heading-white::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/svg/cafe.svg);
  background-size: 10rem;
  background-repeat: no-repeat;
}

.heading-white {
  color: #ffffff;
}
.heading-white span {
  color: #ffffff;
}
.heading-white::after {
  display: none;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5e311e;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-color: #5e311e;
}
.header .header-content {
  position: relative;
}
.header .header-content .header-text h1 {
  text-transform: uppercase;
  text-align: center;
  margin-top: 5rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header .header-content .header-text h1 {
    margin-top: 10rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.6;
  background-color: #000000;
}
@media (min-width: 480px) {
  .header {
    padding: 4rem;
  }
}
@media (min-width: 768px) {
  .header {
    padding: 3rem;
    min-height: 70rem;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 2rem;
  }
}
@media (min-width: 1440px) {
  .header {
    background-size: cover;
  }
}

.notavif.notwebp .header-home {
  background-image: url(../img/page/header_inicio.jpg);
}

.notavif.notwebp .header-about-us {
  background-image: url(../img/page/header_nosotros.jpg);
}

.notavif.notwebp .header-process {
  background-image: url(../img/page/header_procesojpg);
}

.notavif.notwebp .header-menu {
  background-image: url(../img/page/header_menu.jpg);
}

.notavif.notwebp .header-gallery {
  background-image: url(../img/page/header_galeria.jpg);
}

.notavif.notwebp .header-contact {
  background-image: url(../img/page/header_contacto.jpg);
}

.webp .header-home {
  background-image: url(../img/page/header_inicio.webp);
}

.webp .header-about-us {
  background-image: url(../img/page/header_nosotros.webp);
}

.webp .header-process {
  background-image: url(../img/page/header_proceso.webp);
}

.webp .header-menu {
  background-image: url(../img/page/header_menu.webp);
}

.webp .header-gallery {
  background-image: url(../img/page/header_galeria.webp);
}

.webp .header-contact {
  background-image: url(../img/page/header_contacto.webp);
}

.avif .header-home {
  background-image: url(../img/page/header_inicio.avif);
}

.avif .header-about-us {
  background-image: url(../img/page/header_nosotros.avif);
}

.avif .header-process {
  background-image: url(../img/page/header_proceso.avif);
}

.avif .header-menu {
  background-image: url(../img/page/header_menu.avif);
}

.avif .header-gallery {
  background-image: url(../img/page/header_galeria.avif);
}

.avif .header-contact {
  background-image: url(../img/page/header_contacto.avif);
}

.navbar-container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navbar-container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.navbar-container .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .navbar-container .logo {
    width: 20rem;
    margin: 0;
  }
}

.main-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    margin-top: 0;
  }
}
.main-nav .nav-link {
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  transition: all 0.2s ease;
  color: #ffffff;
}
.main-nav .nav-link:hover {
  color: #c39c63;
}
.main-nav .nav-link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .main-nav .nav-link {
    margin-bottom: 0;
    margin-right: 2rem;
  }
  .main-nav .nav-link:last-of-type {
    margin-right: 0;
  }
}

.main-content {
  padding: 5rem 2rem;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-content {
    padding: 10rem;
    position: relative;
    margin-top: -10rem;
  }
}

.icons {
  margin-top: 5rem;
}
.icons .icons-list li img {
  margin: 0 auto;
  width: 12rem;
}
.icons .icons-list li p {
  font-size: 3.2rem;
  font-family: "Bona Nova", serif;
  color: #5e311e;
}
@media (min-width: 768px) {
  .icons .icons-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.gallery-section {
  margin-top: 5rem;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
}
.gallery .gallery-image-container img {
  height: 30rem;
  width: 100%;
  object-fit: cover;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  .gallery .gallery-image-container img {
    height: 30rem;
    width: 30rem;
  }
}
@media (min-width: 1024px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 1024px) and (min-width: 1024px) {
  .gallery .gallery-image-container:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery .gallery-image-container:nth-child(1) img {
    height: 61rem;
    object-position: center;
  }
  .gallery .gallery-image-container:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery .gallery-image-container:nth-child(2) img {
    width: 61rem;
  }
  .gallery .gallery-image-container:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery .gallery-image-container:nth-child(4) img {
    height: 61rem;
  }
  .gallery .gallery-image-container:nth-child(5), .gallery .gallery-image-container:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery .gallery-image-container:nth-child(5) img, .gallery .gallery-image-container:nth-child(6) img {
    width: 61rem;
  }
  .gallery .gallery-image-container:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery .gallery-image-container:nth-child(7) img {
    height: 61rem;
  }
  .gallery .gallery-image-container:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery .gallery-image-container:nth-child(8) img {
    width: 91rem;
  }
  .gallery .gallery-image-container:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery .gallery-image-container:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery .gallery-image-container:nth-child(9) img, .gallery .gallery-image-container:nth-child(10) img {
    width: 61rem;
  }
}

.our-menu {
  padding: 15rem 5rem 10rem 5rem;
  background-image: url(../img/svg/menu_arriba.svg), url(../img/svg/menu_abajo.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 2rem, center 100%, 100%;
  background-size: 200%;
}
@media (min-width: 768px) {
  .our-menu {
    background-size: 100%;
    padding: 15rem 0 10rem 0;
    background-size: 100%;
    background-position: center 2rem, center 92%, 100%;
  }
}
@media (min-width: 1024px) {
  .our-menu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .menu-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu h3 {
  color: #ffffff;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.menu li p {
  font-size: 2.2rem;
  line-height: 1.5;
  color: #ffffff;
}
.menu li p span {
  display: block;
  color: #c39c63;
}
.menu li .item-price {
  font-size: 4rem;
  font-weight: 700;
}

.testimonials {
  padding: 10rem 2rem;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .testimonials {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}
.testimonials .testimonial {
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
  background-color: #ebe7de;
}
.testimonials .testimonial .author {
  text-align: right;
  font-size: 2.4rem;
  color: #5e311e;
  font-family: "Bona Nova", serif;
}

.footer {
  background-color: #5e311e;
}
.footer .footer-content {
  padding: 5rem;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .footer .footer-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.footer-box {
  text-align: center;
  margin-bottom: 3rem;
  font-family: "Bona Nova", serif;
}
.footer-box h3 {
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #c39c63;
  font-family: "Bona Nova", serif;
}
.footer-box p,
.footer-box a {
  font-size: 2rem;
  color: #000000;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}

.copyright {
  text-align: center;
  padding: 2rem;
  font-size: 2rem;
  color: #ffffff;
}

.aboutus-content .image {
  margin-bottom: 5rem;
}
.aboutus-content .image img {
  transform: rotate(-3deg);
  border: 3rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .aboutus-content .image {
    margin-bottom: 0;
  }
}
.aboutus-content .text blockquote {
  margin: 0;
  padding: 1rem;
  font-size: 2rem;
  border-left: 0.5rem solid #5e311e;
}
@media (min-width: 768px) {
  .aboutus-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    align-items: center;
  }
}

.coffee-process {
  margin-bottom: 2rem;
}
.coffee-process .coffee-process-image-container,
.coffee-process .description {
  flex: 1;
}
.coffee-process .description h3 {
  margin: 0;
  text-align: left;
  font-size: 2rem;
  color: #5e311e;
}
.coffee-process .description h3::after {
  content: "";
  display: block;
  width: 40%;
  height: 0.5rem;
  background-color: #c39c63;
}
@media (min-width: 1024px) {
  .coffee-process .description h3 {
    font-size: 3.2rem;
  }
}
.coffee-process:nth-child(even) .description {
  order: -1;
}
.coffee-process:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .coffee-process {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}

.main-content-page-menu {
  padding-bottom: 0;
}
.main-content-page-menu h2 {
  margin: 0;
}
@media (min-width: 768px) {
  .main-content-page-menu .menu-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: flex-end;
  }
}
.main-content-page-menu .menu-content .text p {
  font-size: 2rem;
  line-height: 1.2;
}
@media (min-width: 768px) {
  .main-content-page-menu .menu-content .text {
    margin-bottom: 4rem;
  }
}
@media (min-width: 1024px) {
  .main-content-page-menu .menu-content .text {
    margin-bottom: 8rem;
  }
}
.main-content-page-menu .menu-content .menu-image-container img {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .contact-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}

.contact-form-reservation .field {
  margin-bottom: 2rem;
}
.contact-form-reservation .field:last-of-type {
  margin-bottom: 0;
}
.contact-form-reservation .field label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.contact-form-reservation .field input,
.contact-form-reservation .field select {
  padding: 1rem;
  width: 100%;
  background-color: #ffffff;
  border: 3px solid #5e311e;
}
.contact-form-reservation .field input:focus-visible,
.contact-form-reservation .field select:focus-visible {
  outline: none;
  border: 3px solid #c39c63;
}
.contact-form-reservation .field .btn-reservation {
  all: unset;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
  padding: 1rem 0;
  transition: all 0.2s ease;
  font-size: 2.2rem;
  font-weight: 700;
  background-color: #5e311e;
  color: #ffffff;
}
.contact-form-reservation .field .btn-reservation:hover {
  background-color: #c39c63;
}
.contact-form-reservation .field .btn-reservation:disabled {
  opacity: 0.6;
}

.contact-form-image-container {
  display: none;
}
@media (min-width: 768px) {
  .contact-form-image-container {
    display: block;
  }
}